@import url('https://fonts.googleapis.com/css?family=Bevan|Fredoka+One');

body {
  margin: 0;
  padding: 0 40px;
  background: rgba(27, 85, 107, 0.06);
}

header {
  text-align: center;
  padding: 10px 0px;
}

.letter {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(32%, #45757E), to(#454C7E));
  background: -webkit-linear-gradient(top, #45757E 32%, #454C7E 100%);
  background: -o-linear-gradient(top, #45757E 32%, #454C7E 100%);
  background: linear-gradient(-180deg, #45757E 32%, #454C7E 100%);
  font-size: 7vw;
  -webkit-transition: all ease-in-out .3s;
  -o-transition: all ease-in-out .3s;
  transition: all ease-in-out .3s;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.letter--used {
  background: -webkit-linear-gradient(224deg, #3023AE 0%, #C96DD8 100%);
  background: -o-linear-gradient(224deg, #3023AE 0%, #C96DD8 100%);
  background: linear-gradient(-134deg, #3023AE 0%, #C96DD8 100%);
  text-shadow: 4px 4px 0 rgba(157, 89, 204, 0.67);

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.alphabet {
  font-family: Circular-Bold;
  text-align: center;
  text-transform: uppercase;
}

textarea {
  background: rgba(27, 85, 106, 0.06);
  border: 2px solid rgba(185, 140, 217, 0.13);
  border-radius: 15px;
  color: #7A469F;
  font-family: Avenir-Light;
  font-size: 24px;
  margin-top: 23px;
  letter-spacing: 0;
  padding: 10px 10px;
  width: 100%;
}

textarea:focus {
  outline: none;
}

.header__logo {
  color: #454C7E;
  display: block;
  font-family: Avenir-BlackOblique;
  font-size: 36px;
  letter-spacing: -1px;
  text-decoration: underline;
}

.background__letter {
  color: rgba(185, 140, 217, 0.05);
  font-family: Circular-Bold;
  font-size: 150vh;
  height: 100%;
  left: 15%;
  line-height: 100vh;
  text-transform: uppercase;
  top: 0;
  position: absolute;
  overflow: hidden;
  z-index: -1;
}


